/*slide*/

.fck_detail .list_slide {
    width: 100%;
    position: relative;
	margin-bottom: 20px;
}
.btn_control_tv_slider {
    height: 28px;
    width: 28px;
    background: rgba(255,255,255,0.8);
    position: absolute;
    z-index: 9;
    top: 40%;
    text-align: center;
    line-height: 28px;
    cursor: pointer;
}
.btn_control_tv_slider .ic {
    color: #000;
    vertical-align: middle;
}
.btn_control_tv_slider.prev_slider{
    left: 0;
    background-image: url(images/icons/icon_next_prev_common_slider.png);
    background-position: 0 0;
    background-repeat: no-repeat;     
}
.btn_control_tv_slider.next_slider{
    right: 0;
    background-image: url(images/icons/icon_next_prev_common_slider.png);
    background-position: -28px -28px;
    background-repeat: no-repeat;    
}
.fck_detail .btn_control_tv_slider {
    top: 43%;   
}
/*end slide*/

