/*Rating*/
.block_one_start {
    float: left;
    margin: 0 2px;
    position: relative;
    width: 24px;
    height: 24px;
}

.block_one_start i {
    position: absolute;
    top: 8px;
}

.block_one_start i.ic-star-half-left {
    left: 0;
}

.block_one_start i.ic-star-half-right {
    left: -1px;
}

.star-left,
.star-left a {
    float: left;
}

.star-right,
.star-right a {
    float: left;
}

.star a {
    display: inline-block;
    width: 12px;
    height: 40px;
    float: left;
    overflow: hidden;
    position: relative;
}

/*Comment rating*/
.number_rating {
    width: 62px;
    height: 9px;
    background: url(images/graphics/bg_start.jpg) no-repeat top left;
    margin: 0 0 7px 0;
}
.criteria_cmt {
    margin-bottom: 10px;
    display: block;
    overflow: hidden;
}
.danhgia_comment {
    display: block;
    overflow: hidden;
}
.danhgia_comment .span_fr {
    font: bold 14px arial;
    line-height: 29px;
    float: left;
    margin:5px 20px 0 0;
}
.rating, .block_one_bar {
    float: left;
}

#rating-popup .btn_vne {
    margin-left: 10px;
}

div.rating div.hover a .ic-star-half-right, div.rating div a:hover .ic-star-half-right {
    background-position: -473px -39px;
}

div.rating div.hover a .ic-star-half-left, div.rating div a:hover .ic-star-half-left {
    background-position: -440px -39px;
}

/*Box shop bài detail*/
.box_store_ns {
    ul{
        font-size: 0;margin-left: -20px;
        li{
            display: inline-block;width: 25%;margin-bottom: 20px;padding-left: 20px;vertical-align: top;
            .avata{
                position: relative;
                margin-bottom: 10px;
                display: block;  
                height: 200px;
                img{
                    max-height: 100%;
                    max-width: 100%;
                    width: auto;
                    height: auto;
                    position: absolute;
                    top: 0;
                    bottom: 0;
                    left: 0;
                    right: 0;
                    margin: auto;
                }                  
            }
            .conten{
                h3{font-size: px_to_rem(16px);font-family: $fontmain;font-weight: 400;line-height: 1.4;margin-bottom: 5px; height: 44px;overflow: hidden;}
                .price{
                    .new{font-size: 15px;color: #168fd6;font-weight: bold;}
                    .old{
                       color: #168fd6;font-size: 12px;
                       span{color: #666;text-decoration: line-through;margin-right: 5px;} 
                    }
                }
            }
        }
    }
}
/*End Box shop bài detail*/